@layer {

  @font-face {
    font-family: 'segoe';
    src: url(../assets/segoe.ttf) format('truetype');
  }
  
  body {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    /* height: 150vh;
    width: 150vw; */
    
    margin: 0;
    font-family: 'segoe', 'Segoe UI', sans-serif;
  }
    
  .full #root {
    width: 100%;
    height: 100%;
  }
  
  .app {
    width: 800px;
    height: 600px;

    background-image: url(../assets/global/background.jpg);
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .full .app {
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }

  .head {
    margin: 0;

    font-size: 12px;
    color: rgb(110, 110, 110);
    font-weight: 600;
    text-align: left;
    letter-spacing: 0;
  }
  .head--2 {
    margin-bottom: 4px;
    
    color: rgb(88, 88, 88);
    font-size: 11px;
  }
  .head--underline {
    text-decoration: underline;
  }
  .head--dark {
    color: rgb(88, 88, 88);
  }
  .head--super-bold {
    font-weight: 700;
  }
  
  .clean-list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  .line-break {
    padding-top: 3px;
  }

  .dropdown-container {
    position: relative;
  }

  .rotate-180 {
    transform: rotate(180deg);
  }

  .vertical-line {
    width: 1px;
    height: 100%;
  
    background-color: rgb(227, 228, 229);
  }
  
  .tooltip-container {
    position: relative;
  }
  .tooltip-container--out {
    position: static;
  }
  .tooltip {
    visibility: hidden;
  }
  .tooltip-container:hover .tooltip {
    visibility: visible;
    transition: visibility 0s linear 1000ms;
  }
  .tooltip:hover {
    transition-delay: 0s !important;
    visibility: hidden !important;
  }
 
  .link {
    color: rgb(0, 102, 204);
  }
  
}